html {
  scroll-behavior: smooth;
  font-family: "Times New Roman", Times, serif;
}

.headerTab {
  color: whitesmoke;
  background: transparent;
  border: none !important;
  cursor: pointer;
  font-size: 20px;
  padding: 0 15px;
  float: right;
  padding-top: 2vh;
}

.headerTab:hover {
  transform: scale(1.1);
}

.page {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.title_heading {
  text-align: center;
  padding-top: 40vh;
  font-size: 80px;
  color: white;
}

.subtitle {
  text-align: center;
  padding-top: 5vh;
  font-size: 25px;
  color:white;
}

.langTab {
  color: whitesmoke;
  background: transparent;
  border: none !important;
  cursor: pointer;
  font-size: 20px;
  padding: 0 15px;
  float: left;
  padding-top: 2vh;
}

.langTab:hover {
  transform: scale(1.1);
}

li {
  color: white;
  padding-left: 10vh;
  list-style: none;
  font-family: "Times New Roman", Times, serif;
  font-style: bold;
  font-size: 4vh;
  filter: none;
}

.sub_elem {
  padding-left: 10vh;
  padding-top: 0vh;
  color: rgb(15, 23, 51);
  list-style: none;
  font-style: normal;
  font-size: 1vh;
}

.bullet {
  color: white;
  font-style: oblique;
  font-size: 3vh;
  white-space: normal;
}

.subtitle_heading {
  text-align: center;
  padding-top: 10vh;
  font-size: 45px;
  color: white;
}

.backgroundImg {
  background-image: url('./img/balconies2.jpg');
  background-size: 100% 100%;
  background-repeat: no-repeat;
}

.company {
  color: #BDBDBD;
}

.institution {
  color: #BDBDBD;
}

.icon {
  color: white;
  padding: 5px;
  text-align: center;
  display: inline-block;
  width: 60px;
  height: 60px;
}

.soc_med_icon {
  flex: 50%;
}

.soc_med_bar {
  display: inline-block;
  margin-left: 50%;
}

.icon:hover {
  transform: scale(1.1);
}

.centreImage {
  padding-top: 8px;
}

.bottomPadding {
  padding-bottom: 40vh;
}

.bio_subtitle {
  text-align: center;
  padding-top: 5vh;
  font-size: 25px;
  color:white;

  margin-left: 15vh;
  margin-right: 15vh;
}